.linkCard {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 28%;
  height: 340px;
  position: relative;
  margin-right: 5%;
  margin-bottom: 3%;
  text-decoration: none;
}

.coverCard {
  height: 100%;
  Width: 100%;
  margin-bottom: 20px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 10px;
  z-index: 1;
}

.backgroundCard {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 20px;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
  border-radius: 10px;
  z-index: 2;
}

.titleCard {
  position: absolute;
  padding-left: 20px;
  padding-bottom: 20px;
  font-family: "Monserrat";
  font-weight: 500;
  font-size: 18px;
  width: 55%;
  color: rgba(255, 255, 255, 0.995);
  z-index: 3;
}

@media screen and (max-width: 768px) {
  .linkCard {
    width: 99.5%;
    height: 255px;
    border-radius: 10px;
    position: relative;
    margin-bottom: 10%;
    margin-left: 0.5%;
  }
  .coverCard {
    width: 100%;
    height: 255px;
    border-radius: 10px;
    position: relative;
    margin-bottom: 5px;
  }
  .backgroundCard {
    bottom: 5px;
  }
  .titleCard {
    width: 45%;
  }
}/*# sourceMappingURL=Card.css.map */